.app-wrapper {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 60px 1fr;
    grid-template-areas:
    'h h'
    'n c';
}
.app-wrapper-content {
    background-color: darkgrey;
    grid-area: c;
}
.selected{
    font-weight: bold;
}




